import React from "react";

import "./CenterLayout.styles.scss";

const CenterLayout = ({ children, bottom, banner }) => {
    return (
        <div className="CenterLayout">
            <div className="CenterLayout__inner">
                <span>
                    {children}
                </span>
                <span>
                    <img src={banner} alt="Banner" />
                </span>
            </div>
        </div>
    );
};

export default CenterLayout;