import React from "react";

import "./Button.styles.scss";

const Button = ({type, onClick, children}) => {
    if (type === "transparant") {
        return (
            <button onClick={onClick} className="Button__transparant">{children}</button>
        );
    } else if (type === "solid") {
        return (
            <button onClick={onClick} className="Button__solid">{children}</button>
        );
    }
};

export default Button;