import React from "react";

import "./Input.styles.scss";

const Input = React.forwardRef(({ placeholder, type, size, value, text}, ref) => {
    if (size === "full") {
        return (
            <div className="Input01__Full">
                <input autoComplete="new-password" required type={type} value={value} ref={ref} />
                <small>{placeholder}</small>
                <text>{text}</text>
            </div>
        );
    } else if (size === "half") {
        return (
            <div className="Input01__Half">
                <input autoComplete="new-password" required type={type} value={value} ref={ref} />
                <small>{placeholder}</small>
            </div>
        );
    }
});

export default Input;