import React, { Component } from 'react';
import cookie from "react-cookies";

import Input from "../src/Input/Input.component";
import CenterLayout from "../src/CenterLayout/CenterLayout.component";
import ButtonsInline from "../src/ButtonsInline/ButtonsInline.component";
import Button from "../src/Buttons/Button.component";
import Link01 from "../src/Link/Link01";

import FannstSmall from "../images/fannst-small.png";
import FannstRegbanner from "../images/fannst-regbanner.png";

class Signin extends Component {
    signin = () => {
        let password = this.refs.password.value;
        let username = this.refs.username.value;

        if (
            password !== "" &&
            username !== ""
        ) {
            let xml = new XMLHttpRequest();
            xml.open("POST", "https://fannst.nl/rest/auth/signin");
            xml.setRequestHeader("Content-Type", "application/x-www-form-urlencoded");
            xml.send("username=" + username + "&password=" + password );

            xml.onreadystatechange = function () {
                if (xml.readyState === 4 && xml.status === 200) {
                    let response = JSON.parse(xml.responseText);

                    if (response.status) {
                        // Parses the url
                        let search = window.location.search;
                        search = search.substring(1, search.length);
                        search = search.split("&");
                        search = search.map(function (param) {
                            let slt = param.split("=");
                            return '"' + slt[0] + '": "' + slt[1] + '"';
                        });
                        search = JSON.parse("{" + search.join(",") + "}");
                        // Stores the cookies
                        cookie.save("hid", response.hid);
                        cookie.save("key", response.key);
                        // Redirects
                        if (search.service !== undefined) {
                            window.location.href = "https://" + search.service + ".fannst.nl";
                        } else {
                            window.location.href = "https://fannst.nl";
                        }
                    }
                }
            }
        }
    };

    render () {

        return (<div className="Signin">
            <CenterLayout banner={FannstRegbanner}>
                <img src={FannstSmall} alt="Fannst Small" />
                <h2>Sign in to your Fannst-Account</h2>
                <Input type="text" placeholder="Username" size="full" text="@fannst.nl" ref="username" />
                <Input type="password" placeholder="Password" size="full" ref="password" />
                <ButtonsInline>
                    <Link01 to="/signup" type="transparant">Create your account</Link01>
                    <Button onClick={this.signin} type="solid">Sign in</Button>
                </ButtonsInline>
            </CenterLayout>
        </div>);
    }
}

export default Signin;
