import React from "react";
import { Link } from "react-router-dom";

import "./Link01.scss";

const Link01 = ({type, to, children}) => {
    if (type === "transparant") {
        return (
            <Link to={to} className="Link__transparant">{children}</Link>
        );
    } else if (type === "solid") {
        return (
            <Link to={to} className="Link__solid">{children}</Link>
        );
    }
};

export default Link01;